.group-pagination{display:flex;align-items:center;height:3rem;background:#a66200;color:#fff;font-size:1.2rem}.group-pagination__btn{height:100%;padding:0 1.3rem;border:none;outline:none;background:#0000;font-size:1.2rem;color:#fff;cursor:pointer;transition:.2s}.group-pagination__btn:nth-child(2){background:#ffb140;border-right:1px dashed #fff}.group-pagination__btn:nth-child(3){background:#dea854;border-right:1px dashed #fff}.group-pagination__btn:nth-child(4){background:#ae9b71;border-right:1px dashed #fff}.group-pagination__btn:nth-child(5){background:#818f8c;border-right:1px dashed #fff}.group-pagination__btn:nth-child(6){background:#5d86a1;border-right:1px dashed #fff}.group-pagination__btn:nth-child(7){background:#2777c2}.group-pagination__btn:nth-child(8){border-left:1px dashed #fff;background:#006dd9}.group-pagination__btn:hover{box-shadow:inset 0 0 6px rgba(10,10,10,.5),0 0 6px rgba(65,65,65,.2);outline-color:rgba(255,255,255,0);outline-offset:15px;text-shadow:1px 1px 2px #2a4b58}.group-pagination__text{height:100%;line-height:3rem;border-right:1px dashed #fff;padding:0 1rem}@media screen and (max-width: 520px){.group-pagination__text{display:none}}.tooltip{position:relative}.tooltip:hover:after{display:inline-block}.tooltip::after{z-index:200;display:none;content:"Сложные слова";font-size:.9rem;border-radius:.2rem;padding:.5rem;position:absolute;background:#555;color:#fff;bottom:-3.5rem;left:1.5rem;transform:translate(-50%)}.tooltip__del::after{width:5rem;content:"Удалить из сложных"}.tooltip__add::after{width:5rem;content:"Добавить в сложные"}.tooltip__apply::after{left:-0.5rem;width:5rem;content:"Добавить в изученные"}.tooltip__sound::after{left:1.5rem;width:6.5rem;content:"Воспроизвести пример"}