.card-buttons{display:flex;position:absolute;top:0;right:0;border-radius:0 0 0 .5rem;background-color:#bdd4e7;background-image:linear-gradient(315deg, #bdd4e7 0%, #8693ab 74%)}.card-buttons__btn{width:3rem;height:3rem;border:none;outline:none;background:transparent;border-right:1px solid #6b7486;border-left:1px solid #a8b6d3;cursor:pointer;transition:.2s ease-in-out}.card-buttons__btn:hover{box-shadow:0px -8px 5px -8px rgba(0,0,0,.75) inset;-webkit-box-shadow:0px -8px 5px -8px rgba(0,0,0,.75) inset;-moz-box-shadow:0px -8px 5px -8px rgba(0,0,0,.75) inset}.card-buttons__btn-fix{border-right:none}.card-buttons__btn_tooltip{position:relative}.card-buttons__btn_tooltip:hover:after{display:block}.card-buttons__btn_tooltip::after{z-index:500;display:none;content:"Добавить в сложные";width:6rem;font-size:.9rem;border-radius:.2rem;padding:.5rem;position:absolute;background:#555;color:#fff;bottom:-3.5rem;left:1.5rem;transform:translate(-50%)}.card-buttons__btn-disable{display:none}.card-buttons__btn:last-child{border-right:none}.card-buttons__btn:first-child{border-radius:0 0 0 .5rem}